import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        user: null
    },
    getters: {
        authAuto(state) {
            return state.user
        }
    },
    mutations: {
        SET_USER(state, user) {
            state.user = user
        },
        DElETE_USER (state,user) {
            state.user = null
        }
    },
    actions: {
        setUser({commit},user) {
            commit("SET_USER",user)
        },
        deleteUser({commit}) {
            commit('DElETE_USER')
        }
    },
    modules: {}
})
