<template>
  <canvas class="authorization__canvas" ref="canvas" width="1000px" height="1000px"></canvas>
</template>

<script>
export  default  {
  date(){
    return {

    }
  },
  methods: {
    canvasAnimation() {
      let canvas = this.$refs.canvas;
      let ctx = canvas.getContext('2d');
      let w, h;
      let figures = [];
      let properties = {
        bgColor:`rgb(06, 06, 07)`,
        figureColor: `rgba(192, 192, 192, 1)`,
        figureRadius: 3,
        figureCount: 80,
        figureVelocity: 0.5,
        lineLength:150,
        figureLife:6
      };

      w = canvas.width = innerWidth;
      h = canvas.height = innerHeight;
      function bgFill () {
        ctx.fillStyle = properties.bgColor;
        ctx.fillRect(0,0,w,h);
      }

      function loop() {
        figures.forEach((item => {
          if (item.life < 1) {
            ctx.beginPath();
            ctx.arc(item.x, item.y, properties.figureRadius, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fillStyle = properties.figureColor;
            ctx.fill();
          }else {

            item.life--;
          }
        }));
        bgFill();
        figures.forEach((item => {
          item.x + item.velocityX > w && item.x + item.velocityX > 0 || item.x + item.velocityX < 0 ? item.velocityX *= -1 : item.velocityX;
          item.y + item.velocityY > h && item.y + item.velocityY > 0 || item.y + item.velocityY < 0 ? item.velocityY *= -1 : item.velocityY;
          item.x += item.velocityX;
          item.y += item.velocityY;
          console.log()
        }));
        figures.forEach((item => {
          ctx.beginPath();
          ctx.arc(item.x, item.y, properties.figureRadius, 0, Math.PI * 2);
          ctx.closePath();
          ctx.fillStyle = properties.figureColor;
          ctx.fill();
        }));
        drawLines();
        window.requestAnimationFrame(loop);
      }
      function drawLines () {
        let x1,x2,y1,y2,length,opacity
        for (let i in figures) {
          for(let j in figures) {
            x1 = figures[i].x;
            x2 = figures[j].x;
            y1 = figures[i].y;
            y2 = figures[j].y;
            length = Math.sqrt(Math.pow(x1 - x2,2) + Math.pow(y1 - y2,2));
            if (length < properties.lineLength) {
              opacity =  1 - length/properties.lineLength;
              ctx.lineWidth = '0.5';
              ctx.strokeStyle = `rgba(192, 192, 192, `+opacity+`)`;
              ctx.beginPath();
              ctx.moveTo(x1,y1);
              ctx.lineTo(x2,y2);
              ctx.closePath();
              ctx.stroke();
            }
          }
        }
      }

      function init() {
        for (let i = 0; i < properties.figureCount; i++) {
          let figure = {
            x: Math.random() * w,
            y: Math.random() * h,
            velocityX: Math.random() * (properties.figureVelocity * 2) - properties.figureVelocity,
            velocityY: Math.random() * (properties.figureVelocity * 2) - properties.figureVelocity,
            life:Math.random()*properties.figureLife * 60,
          }
          figures.push(figure);
        }
        loop();

      }


      console.log(figures);
      init();


    }
  },
  mounted() {
    this.canvasAnimation ();
  }
}
</script>

<style lang="scss" scoped>
.authorization__canvas {
  background: rgb(06, 06, 07);
  position: absolute;
  z-index: 0;
}
</style>
