<template>
  <div class="authorization">
    <div class="authorization__wrapper">
      <div class="authorization__container">
        <h2 class="authorization__title">{{title}}</h2>
        <div class="authorization__inp">
          <p @click="" :class="{isActive:visiblePhone}">Введите номер</p>
          <input :class="{error:validateError}" @focus="inpFocusPhone" @blur="inpBlurPhone" v-model="phone" type="tel">
        </div>
        <div class="authorization__inp">
          <p :class="{isActive:visiblePassword}">Введите пароль</p>
          <input :class="{error:validateError}" @focus="inpFocusPassword" @blur="inpBlurPassword" v-model="password"
                 type="text">
        </div>
        <button @click="sendForm" class="authorization__send">Отправить</button>
        <button class="authorization__registration">Регистрация</button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  components: {},
  data() {
    return {
      password: '',
      phone: '',
      title:'Вход',
      visiblePhone: false,
      visiblePassword: false,
      validateError: false,
    }
  },
  methods: {
    inpFocusPhone() {
      this.phone.length === 0 ? this.phone += '+7' : this.phone;
      this.visiblePhone = true;

    },
    inpFocusPassword() {
      this.visiblePassword = true;
    },
    inpBlurPhone() {
      this.phone === '+7' ? this.phone = '' : this.phone;
      this.phone.length !== 0 ? this.visiblePhone = true : this.visiblePhone = false;
    },
    inpBlurPassword() {
      this.password.length !== 0 ? this.visiblePassword = true : this.visiblePassword = false;
    },
    inpValidatePhone() {
      let re = /(\+)([7|8])(\d{10}$)/;
      return re.test(this.phone);
    },
    inpValidatePassword() {
      let re = /^[a-zA-Z0-9]{10}$/;
      return re.test(this.password);
    },
    sendForm() {
      if (this.inpValidatePhone() === false || this.inpValidatePassword() === false ) {
        this.validateError = true
      }else {

        // this.$router.push("/task");
      }
      // console.log(this.inpValidatePhone())
      console.log(this.inpValidatePassword());
    },

  },
  mounted() {
    console.log(this.$api);
  }

}
</script>


<style lang="scss" scoped>
* {
  box-sizing: border-box;
}

.authorization {
  width: 100%;
  height: 100vh;

  position: relative;

  &__wrapper {
    width: 40%;
    max-width: 500px;
    min-height: 400px;

    display: flex;
    justify-content: center;
    align-items: center;


    border: 1px solid black;
    border-radius: 24px;

    background: rgb(192, 192, 192);

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    opacity: .97;


  }

  &__container {
    width: 60%;
    height: 60%;

    margin: 0 0 20px 0;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }

  &__send {
    width: 100%;
    height: 44px;
    background-color: rgb(40, 40, 40);

    font-size: 18px;
    color: rgba(192, 192, 192, 1);
    margin-top: 40px;

    border-radius: 12px;
    border: 1px solid rgba(40, 40, 40, 1);
    cursor: pointer;
  }

  &__registration {
    width: fit-content;
    height: fit-content;
    margin: 10px 0 0 0;
    background: none;
    border: none;
    color: rgb(40, 40, 40);
    font-size: 16px;
    cursor: pointer;

  }

  &__title {
    color: rgb(40, 40, 40);
  }

  &__inp {
    width: 100%;
    position: relative;

  }

  &__inp p {
    font-size: 18px;
    color: rgba(192, 192, 192, 1);

    position: absolute;
    top: -6px;
    left: 20px;
  }

  &__inp input {
    width: 100%;
    height: 44px;

    padding: 10px 0 0 20px;
    margin: 0 0 10px 0;

    font-size: 18px;
    border: 1px solid rgba(40, 40, 40, .9);
    color: rgba(192, 192, 192, 1);
    border-radius: 12px;
    background: rgba(40, 40, 40, .9);
  }

}

.isActive {
  font-size: 12px !important;
  top: -8px !important;
  left: 20px !important;
}

.error {
  border: 2px solid red !important;
}

</style>
